
export default {
  searchSchool: function (name, callback) {
    fetch('https://open.neis.go.kr/hub/schoolInfo?' + new URLSearchParams({
      KEY: 'a7d9b63eda43415e8802a55fa0f4f2d2',
      Type: 'json',
      SCHUL_NM: name
    })).then((response) => response.json())
      .then((rtv) => callback(rtv))
  },
  getMaxClass(school,grade, callback){
    fetch('https://open.neis.go.kr/hub/classInfo?' + new URLSearchParams({
      KEY: 'a7d9b63eda43415e8802a55fa0f4f2d2',
      Type: 'json',
      ATPT_OFCDC_SC_CODE: school.ATPT_OFCDC_SC_CODE,
      SCHUL_NM: school.SCHUL_NM,
      GRADE: grade
    })).then((response) => response.json())
        .then((rtv) => {
          let m = 0
          rtv['classInfo'][1]['row'].forEach(element => {
            m = Math.max(element['GRADE'],m)
          })
          callback(m)
        })
  }
}
