<template>
  <div class="grade-select-modal">
    <h2>학년</h2>
    <br/>
    <div class="grades">
      <div class="grade" v-for="(i) in maxgrade" :key="i" @click="setImsiGrade(i)">
        <p class="name">{{i}} 학년</p>
      </div>
    </div>
  </div>
</template>

<script>
import cookiemanager from "@/assets/js/cookiemanager";

export default {
  name: "GradeSelectModal",
  props: {
    maxgrade: Number
  },
  methods: {
    setImsiGrade: function (i) {
      cookiemanager.setCookie('imsigrade', i, 1)
      this.$emit('close')
    }
  }
}
</script>

<style scoped>
@font-face {
  font-family: 'SpoqaHanSansNeo-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2108@1.1/SpoqaHanSansNeo-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
* {
  font-family: 'SpoqaHanSansNeo-Regular', sans-serif;
}

.grade {
  margin-top: 1px;
  border: 2px solid rgba(189, 189, 189);
  border-radius: 10px;
  height: 50px;
  margin-left: 3%;
  width: 94%;
}
.grade p {
  margin-top: 14px;
}
</style>