<template>
  <div id="setting">
    <div class="goback" @click="history.back()">

    </div>
    <div class="texts">
      <h2>학교 설정</h2>
      <p>학급 시간표를 제공하기 위한</p>
      <p>필수 정보를 입력해주세요.</p>
      <br/>
    </div>

    <transition name="fade" appear>
      <div class="modal-overlay" v-if="selected!=null" @click="selected=null"></div>
    </transition>

    <transition name="pop" appear>
      <div class="modal" role="dialog" v-if="selected!=null">
        <div class="page" v-show="selected==='school'">
          <SchoolSelectModal @close="$forceUpdate();selected=null;"></SchoolSelectModal>
        </div>
        <div class="page" v-show="selected==='grade'">
          <GradeSelectModal :maxgrade="maxgrade" @close="$forceUpdate();selected=null;"></GradeSelectModal>
        </div>
        <div class="page" v-show="selected==='class'">
          <h1>반</h1>
          <p>학교를 선택하세요</p>
          <button @click="selected=null" class="button">Hide Modal</button>
        </div>
      </div>
    </transition>

    <div class="inputbar" @click="selected='school'">
      <p v-if="getSchool('imsi') !== null">{{getSchool('imsi').SCHUL_NM}}</p>
      <p v-else-if="getSchool('main') !== null">{{getSchool('main').SCHUL_NM}}</p>
      <p v-else>학교</p>
    </div>
    <div class="inputbar" @click="select('grade')">
      <p v-if="getGrade('imsi') !== null">{{getGrade('imsi')}} 학년</p>
      <p v-else-if="getGrade('main') !== null">{{getGrade('main')}} 학년</p>
      <p v-else>학년</p>
    </div>
    <div class="inputbar" @click="select('class')">
      <p>반</p>
    </div>
    <div class="fin">
      <p>저장하기</p>
    </div>
  </div>
</template>

<script>
import SchoolSelectModal from '../components/SchoolSelectModal'
import GradeSelectModal from "../components/GradeSelectModal";
import cookiemanager from "@/assets/js/cookiemanager";
import neismanager from "@/assets/js/neismanager";

export default {
  name: "SchoolSetting",
  components: {
    SchoolSelectModal, GradeSelectModal
  },
  data () {
    return {
      selected: null,
      maxgrade: 0,
      maxclass: 0
    }
  },
  methods: {
    getSchool: function (type) {
      if (type === 'main'){
        let mainschoolstr = cookiemanager.getCookie('school')
        return mainschoolstr !== null? JSON.parse(mainschoolstr) : null
      }
      else if (type === 'imsi'){
        let imsischoolstr = cookiemanager.getCookie('imsischool')
        return imsischoolstr !== null? JSON.parse(imsischoolstr) : null
      }else {
        if(this.getSchool('imsi') == null){
          return this.getSchool('main')
        }else {
          return this.getSchool('imsi')
        }
      }
    },
    getGrade: function(type) {
      if (type === 'main') return cookiemanager.getCookie('grade')
      else return cookiemanager.getCookie('imsigrade')
    },
    isSchoolSelected: function () {
      return this.getSchool('all') !== null
    },
    select: function (type) {
      if(this.isSchoolSelected()){
        if(type === 'grade'){
          if(this.getSchool('all').SCHUL_KND_SC_NM === '중학교'){
            this.maxgrade = 3;
          }else {
            this.maxgrade = 6;
          }
        }
        this.selected = type
      }else {
        alert('학교를 먼저 선택해 주세요')
      }
    },
    getMaxClass: function () {
      neismanager.getMaxClass(this.getSchool('all'),3,m => {
        this.maxclass = m
        this.$forceUpdate()
      })
    }
  }
}
</script>

<style scoped>
@font-face {
  font-family: 'SpoqaHanSansNeo-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2108@1.1/SpoqaHanSansNeo-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
#setting {
  text-align: left;
  font-family: 'SpoqaHanSansNeo-Regular',sans-serif;
}
.texts p, h2{
  margin-left: 5%;
}

.goback {
  margin-left: 0;
  height: 25px;
  width: 100%;
  border-bottom-width: thin;
  border-bottom-color: #d0d0d0;
  border-bottom-style: solid;
}

.inputbar {
  margin-left: 5%;
  border: 2px solid rgba(189, 189, 189);
  border-radius: 10px;
  margin-top: 3px;
  height: 50px;
  width: 90%;
}
.inputbar p{
  margin-top: 14px;
  color: black;
  margin-left: 10px;
}

.fin {
  text-align: center;
  margin-left: 5%;
  /*border: 2px solid rgba(189, 189, 189);*/
  border-radius: 10px;

  position: absolute;
  right: 0;
  bottom: 1%;
  left: 0;
  height: 50px;
  width: 90%;

  background-color: #3282f6;
}
.fin p{
  margin-top: 14px;
  color: white;
}

.modal {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto auto 0 auto;
  text-align: center;
  width: 80%;
  height: fit-content;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.2);
  background: #FFF;
  z-index: 999;
  transform: none;
}
.modal h1 {
  margin: 0 0 1rem;
}

.modal-overlay {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 998;
  background: #2c3e50;
  opacity: 0.6;
  cursor: pointer;
}

/* ---------------------------------- */
.fade-enter-active,
.fade-leave-active {
  transition: opacity .4s linear;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.pop-enter-active,
.pop-leave-active {
  transition: transform 0.4s cubic-bezier(0.5, 0, 0.5, 1), opacity 0.4s linear;
}

.pop-enter,
.pop-leave-to {
  opacity: 0;
  transform: translateY(-100px);
}

</style>